import thunk from 'redux-thunk'
import logger from './logger'
import {applyMiddleware} from 'redux'

//UWAGA to plik zawierajacy WSZYSTKIE middlewary
//WAZNE: middlewary powinny byc wypisane w kolejnosci:
//thunk,
//...jakies middlewary
//logger

//THUNK to middleware pozwalajacy, zeby action creator zwrocil nam funkcje, a nie zwykly obiekt
//dziaa tak, e jeli widzi zwyk akcj (ktra jest obiektem), to przekazuje j
// kolejnemu middlewareowi (next). Jeli jednak zobaczy funkcj, to wywouje j 
//(zwykle to funkcje pobierajce dane z API) i z tej funkcji zwracane s akcje. 
//Thunk znowu sprawdza, czy zwracane akcje s obiektami, czy funkcjami i bdzie to powtarza 
//a wszystkie akcje bd obiektami - te przekae do kolejnych middlewarew.

export default applyMiddleware(
  thunk,
  logger
)


